@font-face {
  font-family: "Norms";
  font-display: swap;
  src: url("../fonts/TTNorms-Bold.woff") format("woff"), url("../fonts/TTNorms-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: "Norms";
  font-display: swap;
  src: url("../fonts/TTNorms-ExtraBold.woff") format("woff"), url("../fonts/TTNorms-ExtraBold.woff2") format("woff2");
  font-weight: 800;
  font-style: normal;
}

@font-face {
  font-family: "Norms";
  font-display: swap;
  src: url("../fonts/TTNorms-Medium.woff") format("woff"), url("../fonts/TTNorms-Medium.woff2") format("woff2");
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: "Norms";
  font-display: swap;
  src: url("../fonts/TTNorms-Regular.woff") format("woff"), url("../fonts/TTNorms-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "Norms";
  font-display: swap;
  src: url("../fonts/TTNorms-Light.woff") format("woff"), url("../fonts/TTNorms-Light.woff2") format("woff2");
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: "Rounds";
  font-display: swap;
  src: url("../fonts/RoundsBlack.woff") format("woff"), url("../fonts/RoundsBlack.woff2") format("woff2");
  font-weight: 900;
  font-style: normal;
}

*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  width: 100%;
  background-color: #fff;
  font-family: 'Norms', sans-serif;
  color: #3d4048;
}

h1,
h2,
h3,
h4 {
  margin: 0;
}

img {
  display: block;
  width: 100%;
  height: auto;
  object-fit: cover;
}

ul,
li {
  list-style: none;
}

a {
  text-decoration: none;
}

svg {
  width: 100%;
  height: 100%;
}

.body-wrapper {
  position: relative;
  width: 100%;
  overflow-x: hidden !important;
}

.container {
  position: relative;
  width: 100%;
  max-width: 1260px;
  padding: 0 15px;
  margin: 0 auto;
}

.hidden {
  position: absolute;
  transform: translateY(-300px);
  transition: transform 1s ease-in-out;
}

.first-screen {
  width: 100%;
  background-image: url("../img/header-bg.jpg");
  background-size: cover;
  background-repeat: no-repeat;
}

.first-screen h1 {
  display: none;
}

.title {
  font-weight: 700;
  font-size: 4.8rem;
  color: #000;
}

.title-price {
  width: 100%;
  height: 100%;
  background-image: url(../img/subtract.svg);
  background-repeat: no-repeat;
  background-size: 100% 99%;
  font-weight: 800;
  color: #000;
  display: inline-flex;
  justify-content: center;
  align-items: center;
}

.top-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 200px;
  background: radial-gradient(120% 120% at 50% 0, #f3f3f3 0, rgba(250, 250, 250, 0) 57.29%);
}

.btn {
  width: 100%;
  max-width: 285px;
  height: 70px;
  border-radius: 3px;
  color: #fff;
  font-weight: 500;
  font-size: 1.8rem;
  letter-spacing: 0.05em;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  box-shadow: 0px 30px 30px -15px rgba(40, 74, 183, 0.2);
}

.btn__icon {
  width: 17px;
  height: 17px;
  margin-right: 8px;
}

.btn--blue {
  background: #365edc;
  transition: 0.3s ease-in-out;
}

.btn--blue:hover {
  background: #3b69fa;
}

.btn--gray {
  background: #edeff1;
  color: #000;
}

.header .top-header.sticky {
  position: fixed;
  top: 0;
  transition: transform 0.8s ease-in-out;
  z-index: 30;
  background: #fff;
  width: 100%;
  box-shadow: 0px 30px 30px 0 rgba(40, 74, 183, 0.1);
}

.header__wrap {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 25px 2px;
}

@media (max-width: 450px) {
  .header__wrap {
    padding: 15px 0;
  }
}

.header__logo {
  width: 100%;
  max-width: 180px;
  height: 45px;
}

.header__dropdown {
  max-width: 180px;
  height: 40px;
}

@media (max-width: 980px) {
  .header__dropdown {
    max-width: 120px;
  }
}

@media (max-width: 450px) {
  .header__dropdown {
    display: none;
  }
}

.header__nav {
  width: 100%;
  padding: 25px 55px;
  border-radius: 3px;
  background-color: #365edc;
  box-shadow: 0px 30px 30px -15px rgba(40, 74, 183, 0.2);
}

@media (max-width: 868px) {
  .header__nav {
    display: none;
  }
}

.logo-link {
  display: inline-block;
  width: 100%;
  max-width: 180px;
}

@media (max-width: 450px) {
  .logo-link {
    max-width: 153px;
    height: 40px;
  }
}

@media (max-width: 380px) {
  .logo-link {
    margin-right: 25px;
  }
}

.logo {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo__img {
  width: 40px;
  height: 44px;
}

@media (max-width: 450px) {
  .logo__img {
    max-width: 33px;
    height: 37px;
  }
}

.logo__text {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  font-family: 'Rounds', sans-serif;
  font-size: 3rem;
  line-height: 36px;
  letter-spacing: 0.05em;
  color: #0b1023;
}

@media (max-width: 450px) {
  .logo__text {
    font-size: 2.6rem;
    line-height: 30px;
  }
}

.logo__text span {
  display: block;
  font-family: 'Norms', sans-serif;
  font-weight: 500;
  font-size: 1.1rem;
  line-height: 12px;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  color: #b5bac8;
}

@media (max-width: 450px) {
  .logo__text span {
    font-size: 0.9rem;
    line-height: 9px;
    margin-right: 6px;
  }
}

.social {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.social__whatsapp {
  display: inline-block;
  width: 100%;
  max-width: 130px;
}

@media (max-width: 920px) {
  .social__whatsapp {
    max-width: 40px;
  }
}

.social__whatsapp:hover .whatsapp {
  fill: #46f059;
}

.social__whatsapp:hover .social__name {
  color: #365edc;
}

.social__whatsapp .whatsapp {
  fill: #43d854;
  transition: fill 0.3s ease-in-out;
}

@media (max-width: 450px) {
  .social__whatsapp .whatsapp {
    width: 30px;
    height: 30px;
  }
}

.social__telegram {
  display: inline-block;
  width: 100%;
  max-width: 120px;
}

@media (max-width: 920px) {
  .social__telegram {
    max-width: 40px;
  }
}

.social__telegram:hover .telegram {
  fill: #09a2ef;
}

.social__telegram:hover .social__name {
  color: #365edc;
}

.social__telegram .telegram {
  fill: #0088cc;
  transition: fill 0.3s ease-in-out;
}

@media (max-width: 450px) {
  .social__telegram .telegram {
    width: 30px;
    height: 30px;
  }
}

.social__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 40px;
}

.social__img {
  width: 40px;
  height: 40px;
}

@media (max-width: 450px) {
  .social__img {
    width: 30px;
    height: 30px;
  }
}

.social__name {
  font-weight: 400;
  font-size: 1.6rem;
  color: #3d4048;
  transition: color 0.3s ease-in-out;
}

@media (max-width: 920px) {
  .social__name {
    display: none;
  }
}

.help {
  display: none;
}

@media (max-width: 450px) {
  .help {
    display: block;
  }
}

.telephone {
  font-weight: 700;
  font-size: 2.4rem;
  line-height: 28px;
  letter-spacing: 0.02em;
}

@media (max-width: 920px) {
  .telephone {
    font-size: 2.2rem;
  }
}

@media (max-width: 768px) {
  .telephone {
    font-size: 2rem;
  }
}

@media (max-width: 657px) {
  .telephone {
    display: none;
  }
}

.modal {
  position: absolute;
  z-index: 30;
  top: 0;
  left: 0;
  width: 100%;
}

.modal-content {
  background: #fff;
  width: 100%;
  animation-name: animatefade;
  animation-duration: 1s;
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 70px;
  padding: 20px 15px;
  border-bottom: 1px solid #ebeff6;
}

.modal-header .close {
  width: 12px;
  height: 12px;
}

.modal-header .help__img {
  width: 30px;
  height: 30px;
}

.modal-body ul {
  padding: 0 15px;
}

.modal-body ul li {
  height: 50px;
}

.modal-body ul li:nth-child(1) svg {
  fill: #46f059;
}

.modal-body ul li:nth-child(2) svg {
  fill: #0088cc;
}

.modal-body ul li:nth-child(4) svg {
  fill: #365edc;
}

.modal-body ul li:not(:last-child) {
  border-bottom: 1px solid #ebeff6;
}

.modal-body .modal-link {
  height: 100%;
  display: flex;
  align-items: center;
}

.modal-body .modal-link svg {
  width: 30px;
  height: 30px;
  margin-right: 10px;
}

.modal-body .modal-link span {
  font-weight: 400;
  font-size: 1.6rem;
  line-height: 18px;
  color: #3d4048;
}

.open-popup {
  position: relative;
  z-index: 25;
  cursor: pointer;
}

@keyframes animatefade {
  from {
    top: -300px;
    opacity: 0;
  }
  to {
    top: 0;
    opacity: 1;
  }
}

.dropdown {
  width: 100%;
  border: 1px solid #ebeff6;
  border-radius: 3px;
}

.dropdown__title {
  position: relative;
  z-index: 21;
  width: 100%;
  height: 100%;
  padding: 15px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 1.5rem;
  background: #fff;
  color: #3d4048;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}

.dropdown__title.title--header {
  padding: 15px 45px;
}

@media (max-width: 980px) {
  .dropdown__title.title--header {
    padding: 15px 18px;
  }
}

.dropdown__title-icon {
  width: 11px;
  height: 7px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  fill: #365edc;
  transition: transform 0.3s ease-in-out;
}

.dropdown__content {
  position: relative;
  z-index: 20;
  width: 100%;
  height: auto;
  top: -2px;
  max-height: 0;
  overflow: hidden;
  box-shadow: 0px 10px 20px rgba(17, 27, 53, 0.12);
  font-size: 12px;
  color: #748888;
  transition: transform 0.8s ease-in-out;
}

.content__item {
  width: 100%;
  height: 40px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 0 17px 5px 20px;
  background: #fff;
  transition: background 0.3s ease-in-out;
}

.content__item:hover {
  background: #f2f5fb;
}

.content__link {
  display: inline-block;
  font-size: 1.5rem;
  color: #3d4048;
}

.content__link:hover {
  color: #365edc;
}

.dropdown.active .dropdown__title {
  background: #365edc;
  color: #fff;
  border-top-right-radius: 3px;
  border-top-left-radius: 3px;
}

.dropdown.active .dropdown__content {
  max-height: 100vh;
  transform: translateY(0px);
  transition: max-height 0.5s ease-in-out;
}

.dropdown.active .dropdown__title-icon {
  transform: rotate(-180deg);
  transform-origin: center;
  fill: #b5bac8;
}

.dropdown.dropdown--white.active .dropdown__title {
  background: #fff;
  color: #b5bac8;
  border-top-right-radius: 3px;
  border-top-left-radius: 3px;
}

.dropdown.dropdown--white.active .dropdown__title-icon {
  transform: rotate(-180deg);
  transform-origin: center;
  fill: #365edc;
}

.dropdown.dropdown--white .dropdown__content {
  width: 100.8%;
  transform: translateX(-1px);
  box-sizing: border-box;
  border: 1px solid #ebeff6;
  box-shadow: 0px 20px 20px -10px rgba(17, 27, 53, 0.12);
  border-radius: 0px 0px 5px 5px;
}

.navigation__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navigation__link {
  display: inline-block;
  font-weight: 700;
  font-size: 1.6rem;
  letter-spacing: 0.02em;
  color: #fff;
  transition: color 0.3s ease-in-out;
}

.navigation__link:hover {
  color: #ffd600;
}

